.note {
  line-height: 1.25;
  margin: 1.5rem 0;
  border: solid rgba(0,0,0,0.33);
  border-width: 1px 0;
  padding: 0.25rem 0;

  @media screen and (max-width: 50em) {
    &:first-of-type {
      border-top: 0 none;
      padding-bottom: 0.25rem;
    }
  }
}

p.note {
  color: lighten($c-text, 10%);
  font-size: 1rem;
  padding: 0.75rem 0;
}
